@font-face {
  font-family: Sohne;
  src: url(./fonts/soehne-buch.woff2);
  font-weight: 400;
}

@font-face {
  font-family: SohneMono;
  src: url(./fonts/soehne-mono-buch.woff2);
  font-weight: 400;
}

@font-face {
  font-family: Sohne;
  src: url(./fonts/soehne-kraftig.woff2);
  font-weight: 500;
}

@font-face {
  font-family: Sohne;
  src: url(./fonts/soehne-halbfett.woff2);
  font-weight: 600;
}

:root {
  --font-size-xs: 0.75rem;
  --font-size-s: 0.875rem;
  --font-size-m: 1rem;
  --font-size-l: 1.125rem;
  --font-size-xl: 2rem;
  --font-size-xxl: 2.25rem;
  --clr-text-primary: rgba(52, 53, 65, 1);
  --clr-text-secondary: rgba(142, 142, 160, 1);
  --border-radius: 0.375rem;
  --border-color: rgba(0, 0, 0, 0.1);
  --box-shadow: 0 0 transparent, 0 0 transparent, 0 0 transparent,
    0 0 transparent, 0 0 10px rgba(0, 0, 0, 0.1);
  --font-family: Sohne, ui-sans-serif, system-ui, -apple-system, Segoe UI,
    Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, Helvetica Neue, Arial,
    Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
}

html {
  height: 100%;
}

* {
  box-sizing: border-box;
}

:focus {
  outline: 0;
}

label,
p {
  padding: 0;
  margin: 0;
}

body {
  height: 100%;
  margin: 0;
  font-family: var(--font-family);
  color: var(--clr-text-primary);
}

#root {
  height: 100%;
  width: 100%;
}

.h-6 {
  height: 1.5rem;
}

.w-6 {
  width: 1.5rem;
}

.main-con {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: auto;
  padding-bottom: 100px;
}

.main-subcon {
  width: 100%;
  max-width: 768px;
  height: 100%;
  padding: 0px 24px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.main-subcon--absolute {
  width: 100%;
  max-width: 768px;
  height: auto;
  padding: 0px 24px;
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 0;
}

.main-subcon--full-user {
  width: 100%;
  height: auto;
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  flex: 0 0 auto;
  display: flex;
  justify-content: center;
}

.main-subcon--full-response {
  width: 100%;
  height: auto;
  background-color: rgba(247, 247, 248, 1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  flex: 0 0 auto;
  display: flex;
  justify-content: center;
}

.banner-con {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
  justify-content: center;
  align-items: center;
}

.banner-app-title {
  font-size: var(--font-size-xxl);
  font-weight: 600;
  margin-bottom: 64px;
}

.banner-img {
  width: auto;
  max-width: 100%;
  height: 256px;
  border-radius: var(--border-radius);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.banner-img img {
  height: 100%;
  width: auto;
}

.banner-card-con {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-between;
}

.banner-card {
  height: auto;
  width: calc((100% / 3) - 8px);
}

.banner-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.875rem;
}

.banner-header-title {
  font-size: var(--font-size-l);
  font-weight: 400;
  color: var(--clr-text-primary);
}

.banner-content {
  display: flex;
  flex-direction: column;
  gap: 0.875rem;
}

.banner-card-items {
  padding: 12px;
  background-color: rgba(247, 247, 248, 1);
  border-radius: 0.375rem;
  margin-bottom: 0.875rem;
  font-size: var(--font-size-s);
  text-align: center;
}

.chat-main-con {
  position: relative;
  bottom: 0;
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.chat-input-con {
  height: auto;
  min-height: 46px;
  max-height: 200px;
  width: 100%;
  padding: 12px 0px 12px 16px;
  border-radius: var(--border-radius);
  border: 1px solid var(--border-color);
  box-shadow: var(--box-shadow);
  margin-bottom: 8px;
  display: flex;
  position: relative;
  bottom: 0;
  background-color: #fff;
  overflow: hidden;
}

.chat-input-subcon {
  height: auto;
  max-height: calc(100% - 26px);
  width: 100%;
  overflow: auto;
}

.chat-input {
  height: auto;
  width: 100%;
  border: 0;
  padding: 0;
  padding-right: 40px;
  margin: 0;
  font-size: var(--font-size-m);
  font-weight: 400;
  line-height: 24px;
  resize: none;
  font-family: var(--font-family);
  color: var(--clr-text-primary);
  overflow: hidden;
}

.chat-send {
  border: 0;
  background-color: transparent;
  cursor: pointer;
  height: auto;
  width: auto;
  padding: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--clr-text-secondary);
  position: absolute;
  bottom: 12px;
  right: 16px;
}

.footer-notes {
  font-size: var(--font-size-xs);
  font-weight: 400;
  color: var(--clr-text-secondary);
  margin-bottom: 12px;
}

.chat-content {
  width: 100%;
  max-width: 768px;
  padding: 24px;
  display: flex;
}

.chat-icon--user {
  height: 30px;
  width: 30px;
  background-color: #c2185b;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  max-width: 30px;
  overflow: hidden;
}

.chat-icon--response {
  height: 30px;
  width: 30px;
  background-color: #c2185b;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.chat-text {
  padding: 0 24px;
  width: calc(100% - 78px);
  font-size: var(--font-size-m);
}

.chat-response {
  position: relative;
  overflow: hidden;
  animation: typing 1s steps(30, end) 3.5s forwards;
  display: inline-block;
}

.chat-response p {
  visibility: hidden;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
}

.chat-response::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  margin-top: -1px;
  font-size: var(--font-size-m);
  font-family: SohneMono;
  animation: blink 0.7s step-end 5;
  animation-fill-mode: forwards;
  width: 0.6em;
  height: 100%;
  background-color: var(--clr-text-primary);
}

.chat-response-image {
  height: 200px;
  width: 200px;
  margin-top: 6px;
  border-radius: var(--border-radius);
  display: none;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.chat-response-child {
  visibility: hidden;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
}

.search-result {
  width: 100%;
  height: 500px;
}

@keyframes blink {
  from,
  to {
    background-color: transparent;
  }
  50% {
    background-color: var(--clr-text-primary);
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@media only screen and (max-width: 768px) {
  .banner-card-con {
    flex-direction: column;
  }

  .banner-con {
    padding: 100px 0;
  }

  .banner-card {
    width: 100%;
  }

  .banner-app-title {
    margin-bottom: 24px;
  }

  .chat-text {
    width: 100%;
  }
}
